import Vue from 'vue'
import Router from 'vue-router'
import showBlog from '@/components/showBlog'
import addBlog from '@/components/addBlog'
import singeBlog from '@/components/singeBlog'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'showBlog',
      component:showBlog
    },
    {
      path: '/add',
      name: 'addBlog',
      component: addBlog
    },
    {
      path: '/blogs/:id',
      name: 'singeBlog',
      component: singeBlog
    }
  ],
  mode:"history"
})



