<template>
	<div class="singBlog">
		<h2>{{ blog.title }}</h2>
		<article>{{ blog.body }}</article>
	</div>
</template>

<script>
	export default{
		name:'singBlog',
		data(){
			return {
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get('https://jsonplaceholder.typicode.com/posts/'+this.id)
			.then(function(data){
				this.blog = data.body;
			})
		}
	}
</script>

<style scoped>
.singBlog{
	background-color: #eee;
	padding: 10px 20px;
	margin: 30px auto;
	max-width: 600px;
	min-height: 170px
}
</style>


