<template>
	<div class="showBlog" v-theme="'widther'">
		<h1>博客总览</h1>
		<h2 v-theme:test=''>测试自定义指令</h2>
    <input type="text" name="" placeholder="搜索" v-model="searcher">
		<div v-for="blog in filteredBlogs"  class="blogs">
			<router-link v-bind:to="'/blogs/' + blog.id"><h2 v-rainbow>{{ blog.title | to-uppercase}}</h2></router-link>
			<article>{{ blog.body | snippet}}</article>
		</div>
    <div class="blogs" v-if='noData'>没有搜索到结果!</div>
	</div>
</template>

<script>
export default {
  name: 'App',
  data(){
  	return {
  		blogs:[],
      searcher:'',
      noData:false
  	}
  },
  created(){
  	this.$http.get('https://jsonplaceholder.typicode.com/posts')
  	.then(function(data){
  		this.blogs = data.body.slice(0,10)
  	})
  },
  computed:{
    filteredBlogs:function(){
      var matchE = this.blogs.filter((blog)=>blog.title.match(this.searcher));
      if (matchE != '') {
        this.noData = false;
         return matchE //搜索匹配
      }
      else{
        this.noData = true;
      }
    }
  },
  filters:{
    'to-uppercase':function(value){
      return value.toUpperCase()
    }
  },
  directives:{
    'rainbow':{
      bind(el,binding,vnode){
        el.style.color = "#" + Math.random().toString(16).slice(2,8)
      }
    }
  }
}
</script>

<style scoped>
.showBlog{
	margin: 0 auto;
	max-width: 600px
}
.blogs{background-color: #eee;padding: 10px 20px;margin-top:20px;}
.blogs a{text-decoration: none;}
.blogs a:hover{text-decoration: underline;}
input[type='text']{width: 100%;padding: 8px;box-sizing: border-box;}
</style>



