<template>
  <div class="addBlog">
    <h1>写博客</h1>
    <form v-if="!submitted">
        <label>博客标题</label>
        <input type="text" name="title" v-model='blog.title' required>
        <label>博客内容</label>
        <textarea v-model='blog.content'></textarea>
        <div id="checkboxes" >
            <label>Vue.js</label>
            <input type="checkbox" name="" value="Vue" v-model="blog.arr">
            <label>Node.js</label>
            <input type="checkbox" name="" value="node" v-model="blog.arr">
            <label>React.js</label>
            <input type="checkbox" name="" value="react" v-model="blog.arr">
            <label>Angular.js</label>
            <input type="checkbox" name="" value="angular" v-model="blog.arr">
       </div>
            <label>作者:</label>
            <select v-model="blog.author">
              <option>请选择</option>
              <option v-for="author in authors">{{ author }}</option>
            </select>
            <button v-on:click.prevent="add()">添加博客</button>
    </form>
    <div v-if="submitted"><h2>博客发布成功!</h2></div>
    <hr>
    <div id="preview">
      <h2>博客总览:</h2>
      <p>博客标题:{{ blog.title }}</p>
      <p>博客内容:</p>
      <p>{{ blog.content }}</p>
      <ul>
        <li v-for="lan in blog.arr">
          {{ lan }}
        </li>
      </ul>
      <p>作者:{{ blog.author }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      blog:{
        title:'',
        content:'',
        arr:[],
      },
      authors:['rx','aaa','bbb'],
      submitted:false
    }
  },
  methods:{
    add(){
      this.$http.post('https://jsonplaceholder.typicode.com/posts',{
        title:this.blog.title,
        body:this.blog.content,
        userId:1
      }).then(function(data){
        console.log(data)
        this.submitted = true
      })
    }
  }
}
</script>

<style scoped>
.addBlog *{box-sizing: border-box;}
.addBlog{max-width: 600px;margin: 20px auto;padding: 20px}
input[type='text'],textarea,select{
  width: 100%;
  display: block;
  padding: 8px
}
textarea{height: 200px}
label{
  display: block;
  margin:20px 0 10px;
}
#checkboxes{margin-top: 10px}
#checkboxes label{
  display: inline-block;
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px
}
button{
  padding: 8px;
  background-color: crimson;
  color: white;
  border: none;
  border-radius: 4px;
  margin-top: 8px;
  cursor: pointer;
  font-size: 16px
}
#preview{
  border: 1px dotted #ccc;
  padding: 10px 20px;
  margin: 30px 0;
  list-style: none;
}
</style>



